.resources {
    @extend %flex;

    @include transition(all);

    position: relative;

    flex-flow: column nowrap;
    align-content: flex-start;
    justify-content: space-between;

    width: 100%;

    @include mq(lg) {
        flex-flow: row wrap;
    }

    .resource {
        @extend %flex;

        @include transition(all);

        position: relative;
        top: 0;
        left: 0;

        overflow: hidden;

        flex-direction: row;
        align-items: center;

        width: 100%;
        max-height: 175px;
        margin-bottom: $spacingBase;
        padding: $spacingBase;

        cursor: pointer;

        background: rgba($colorBlack, .05);
        box-shadow: none;

        @include mq(lg) {
            width: 49%;
        }
        @include mq(xsm-only) {
            flex-direction: column;

            max-height: 400px;
        }

        &:hover {
            top: -5px;
            left: -5px;

            box-shadow: 4px 4px 6px rgba($colorBlack, .17);
        }
        .resource-image {
            overflow: hidden;

            flex-shrink: 0;

            margin: 0;
            padding: 0;

            img {
                max-width: 100%;
            }
        }
        .resource-name {
            margin: 0 0 0 $spacingLg;
            padding: 0;

            @include mq(xsm-only) {
                margin: 0;
            }

            * {
                margin: 0;
                padding: 0;
            }
            p {
                display: none;
            }
            h4,
            h4 a {
                @extend %transition;

                color: darken($colorBrandBlue, 10%);

                @include mq(xsm-only) {
                    text-align: center;
                }

                &:hover {
                    color: darken($colorBrandBlue, 30%);
                }
            }
        }
    }
}
