body {
    @extend %flex;

    position: relative;

    overflow-x: hidden;

    flex-direction: column;

    min-width: 100%;
    min-height: 100%;
    margin: 0;

    font-family: $fontBase;
    font-size: $fontSizeBase;

    color: $colorBrandDark;
    background: $colorBrandBlue;

    * {
        box-sizing: border-box;
    }
}

body.nav-stuck {
    .navMain {
        position: fixed;
        z-index: 1000;
        top: 0;
        left: 0;

        width: 100%;

        transform: translateZ(1px);

        > ul {
            > li {
                > a {
                    &:after {
                        @extend %iconStyle;
                        @extend %transition;

                        position: absolute;
                        right: auto;
                        bottom: 3px;
                        left: 45%;

                        margin: 0 auto;
                        padding: 0;

                        content: '\e805';
                        vertical-align: bottom;
                        font-size: 20px;
                        line-height: 14px;

                        opacity: 0;
                        color: $colorBrandDark;
                    }
                    &.is-active {
                        &:after {
                            opacity: 1;
                        }
                    }
                }
                &:hover {
                    > a {
                        &.is-active {
                            &:after {
                                color: $colorBrandLight;
                            }
                        }
                    }
                }
            }
        }
    }
}
