figure {
    position: relative;

    float: left;
    clear: both;

    margin: 0;
    margin-bottom: $spacingBase;

    @include mq(med) {
        max-width: 25%;
    }

    img {
        position: relative;

        display: block;

        max-width: 100%;
        margin-right: auto;
        margin-left: auto;
    }
    figcaption {
        @include fontSize(14px);

        position: static;

        caption-side: bottom;

        text-align: center;
        font-style: italic;

        color: $colorBrandDark;
    }
    &.captionTop {
        figcaption {
            caption-side: top;
        }
    }
    &.figure--full {
        max-width: 100%;

        @include mq(med) {
            max-width: 40%;
        }
    }
}
