//Memic Headings
.h-h1 {
    @extend h1;
}
.h-h2 {
    @extend h2;
}
.h-h3 {
    @extend h3;
}
.h-h4 {
    @extend h4;
}
.h-h5 {
    @extend h5;
}
.h-h6 {
    @extend h6;
}


//Text Align
.h-alignLeft {
    text-align: left !important;
}
.h-alignCenter {
    text-align: center !important;
}
.h-alignRight {
    text-align: right !important;
}
.h-justify {
    text-align: justify !important;
}


//Padding
.h-push {
    padding: $spacingBase !important;
}

.h-pushEnds {
    padding: $spacingBase 0 !important;
}

.h-pushSides {
    padding: 0 $spacingBase !important;
}

.h-pushBottom {
    padding: 0 0 $spacingBase !important;
}

.h-pushTop {
    padding: $spacingBase 0 0 0 !important;
}

.h-pushLeft {
    padding: 0 0 0 $spacingBase !important;
}

.h-pushRight {
    padding: 0 $spacingBase 0 0 !important;
}

.h-pushLg {
    padding: $spacingLg !important;
}

.h-pushLgEnds {
    padding: $spacingLg 0 !important;
}

.h-pushLgSides {
    padding: 0 $spacingLg !important;
}

.h-pushLgBottom {
    padding: 0 0 $spacingLg !important;
}

.h-pushLgTop {
    padding: $spacingLg 0 0 0 !important;
}

.h-pushLgLeft {
    padding: 0 0 0 $spacingLg !important;
}

.h-pushLgRight {
    padding: 0 $spacingLg 0 0 !important;
}

.h-pushSm {
    padding: $spacingSm !important;
}

.h-pushSmEnds {
    padding: $spacingSm 0 !important;
}

.h-pushSmSides {
    padding: 0 $spacingSm !important;
}

.h-pushSmBottom {
    padding: 0 0 $spacingSm !important;
}

.h-pushSmTop {
    padding: $spacingSm 0 0 0 !important;
}

.h-pushSmLeft {
    padding: 0 0 0 $spacingSm !important;
}

.h-pushSmRight {
    padding: 0 $spacingSm 0 0 !important;
}

.h-flushBottom {
    margin-bottom: 0 !important;
}

.h-flushTop {
    margin-top: 0 !important;
}

.h-flushLeft {
    margin-left: 0 !important;
}

.h-flushRight {
    margin-right: 0 !important;
}

.h-flush {
    margin: 0 !important;
}

.h-center {
    display: block !important;

    margin-right: auto !important;
    margin-left: auto !important;
}
.isolated {
    margin: $spacingBase !important;

    &--xlg {
        margin: $spacingXLg !important;
    }
    &--lg {
        margin: $spacingLg !important;
    }
    &--sm {
        margin: $spacingSm !important;
    }
    &--xsm {
        margin: $spacingXSm !important;
    }
}

.float-left {
    float: left !important;

    &--lg {
        float: none !important;

        @include mq(lg) {
            float: left !important;
        }
    }
    &--med {
        float: none !important;

        @include mq(med) {
            float: left !important;
        }
    }
    &--sm {
        float: none !important;

        @include mq(sm) {
            float: left !important;
        }
    }
}

.float-right {
    float: right !important;

    &--lg {
        float: none !important;

        @include mq(lg) {
            float: right !important;
        }
    }
    &--med {
        float: none !important;

        @include mq(med) {
            float: right !important;
        }
    }
    &--sm {
        float: none !important;

        @include mq(sm) {
            float: right !important;
        }
    }
}

.clearfix:after {
    display: block !important;
    visibility: hidden !important;
    clear: both !important;

    height: 0 !important;

    content: ' ' !important;
    font-size: 0 !important;
}

.roundSm {
    border-radius: 10px;
}
