.navMain {
   @include fontSize($fontSizeLg);
   position: relative;

   display: none;

   font-family: $fontHighlight;

   background: $colorBrandLight;

   @include mq(lg) {
      display: flex;
   }

   &:after {
      position: absolute;
      top: 100%;
      left: 0;

      width: 100%;
      height: 4px;
      margin-top: -4px;

      content: '';

      background-image: repeating-linear-gradient(to right,
      $colorBrandYellow, $colorBrandYellow 75px,
      $colorBrandBlue 75px, $colorBrandBlue 125px,
      $colorBrandDark 125px, $colorBrandDark 200px,
      $colorBrandMid 200px, $colorBrandMid 275px,
      $colorBrandBlue 275px, $colorBrandBlue 300px,
      $colorBrandYellow 300px, $colorBrandYellow 350px,
      $colorBrandDark 350px, $colorBrandDark 400px,
      $colorBrandMid 400px, $colorBrandMid 425px);
   }
   > ul {
      @extend %contained;
      @extend %unstyledList;
      @extend %flex;
      flex-direction: row;
      justify-content: space-around;

      font-weight: 300;
      line-height: $fontSizeLg;

      > li {
         @extend %flex;
         position: relative;

         flex: 1 1 auto;
         justify-content: center;

         cursor: pointer;

         > a {
            @include transition(color .1s);
            position: relative;
            z-index: 1;

            display: block;
            overflow: hidden;

            flex: 1 1 auto;

            width: 100%;
            padding: $spacingBase;

            text-align: center;
            text-decoration: none;
            font-size: inherit;

            color: $colorBrandDark;
            text-shadow: 1px 1px 1px rgba(127, 127, 127, .3);

            > span {
               @include transition(all .45s);
               position: absolute;
               z-index: -1;
               top: 0;
               right: 0;
               bottom: 0;
               left: 0;

               width: 150px;
               height: 150px;
               margin: auto;

               transform: scale(0, 0);

               opacity: 0;
               border-radius: 50%;
               background: darken($colorBrandBlue, 15%);
            }
         }
         &.has-children {
            > a {
               &:before {
                  @extend %iconStyle;
                  @extend %transition;
                  margin: 0;
                  padding-right: 5px;

                  content: '\e804';
                  transform: rotate(0deg);
                  transform-origin: top left;
               }
            }
         }
         > ul {
            @extend %unstyledList;
            @extend %flex;
            @extend %transition;
            position: absolute;
            z-index: 100;
            top: 100%;
            left: 0;

            flex-direction: column;

            min-width: 100%;
            padding: 0;

            transform: scale(1, 0);
            transform-origin: top;

            background: darken($colorBrandBlue, 10%);

            > li {
               @include transition(background);
               width: 100%;
               margin: 0;
               padding: 0;

               > a {
                  @include fontSize(14px);
                  display: block;

                  width: 100%;
                  height: 100%;
                  margin: 0;
                  padding: $spacingBase $spacingSm;

                  white-space: nowrap;
                  text-decoration: none;

                  color: $colorBrandLight;
               }
               &:hover {
                  background: darken($colorBrandBlue, 20%);
               }
            }
				 }
				 &:last-child {
					 & > ul {
						 left: auto;
						 right: 0;
					 }
				 }
      }
   }
}


.navMain {
   > ul {
      > li {
         &:hover {
            > a {
               color: $colorBrandLight;

               > span {
                  transform: scale(2, 2);

                  opacity: 1;
               }
            }
         }
         &.has-children:hover {
            > a {
               &:before {
                  transform: rotate(90deg);
               }
            }
            > ul {
               transform: scale(1, 1);
            }
         }
      }
   }
}
