.locationInfo {
    h4 {
        margin-bottom: 0;
    }
    p {
        @include fontSize(16px, false);

        line-height: 1.8;

        &:not(.map) {
            @extend .h-pushLgLeft;
        }
        &.subtle {
            @include fontSize(12px);

            margin: 0;
            margin-left: $spacingBase;
        }
        span {
            margin-right: $spacingSm;

            font-weight: bold;
        }
        &.map {
            overflow-x: hidden;
        }
    }
    &--address {
        h4 {
            &:before {
                @extend %iconStyle;

                content: '\e803';
            }
        }
    }
    &--hours {
        h4 {
            &:before {
                @extend %iconStyle;

                content: '\e80b';
            }
        }
    }
}
