.headerMain {
    @extend %flex;

    position: relative;

    flex: 0 0 auto;

    width: 100%;
    min-height: 127px;
    padding-bottom: $spacingBase;

    font-family: $fontHighlight;

    color: $colorBrandDark;
    background: $colorBrandLight;

    @include mq(lg) {
        padding-bottom: none;

        &:after {
            display: none;
        }
    }

    &:after {
        position: absolute;
        top: 100%;
        left: 0;

        width: 100%;
        height: 3px;
        margin-top: -3px;

        content: '';

        background-image: repeating-linear-gradient(to right,
        $colorBrandYellow, $colorBrandYellow 75px,
        $colorBrandBlue 75px, $colorBrandBlue 125px,
        $colorBrandDark 125px, $colorBrandDark 200px,
        $colorBrandMid 200px, $colorBrandMid 275px,
        $colorBrandBlue 275px, $colorBrandBlue 300px,
        $colorBrandYellow 300px, $colorBrandYellow 350px,
        $colorBrandDark 350px, $colorBrandDark 400px,
        $colorBrandMid 400px, $colorBrandMid 425px);
    }
    .headerMain-content {
        @extend %contained;
        @extend %flex;

        flex-direction: column;
        align-items: center;
        justify-content: space-between;

        padding: 0 $spacingBase;

        @include mq(lg) {
            flex-direction: row;
        }

        .headerMain-logo {
            order: 2;
            cursor: pointer;

            @include mq(lg) {
                order: 1;
            }

            h1 {
                margin-bottom: 0;

                text-shadow: 1px 1px 1px rgba(127, 127, 127, .6);

                span {
                    margin-right: 15px;

                    color: $colorBrandYellow;

                    @include mq(xsm-only) {
                        display: block;

                        margin-right: 0 !important;
                        margin-left: 0 !important;
                    }

                    &:first-child {
                        color: $colorBrandBlue;
                    }
                    &:last-child {
                        display: block;

                        margin-left: $spacingLg;

                        white-space: nowrap;

                        color: $colorBrandDark;
                    }
                }
            }
        }
        .headerMain-utilityNav {
            @extend %flex;

            align-self: flex-end;
            order: 1;

            border-radius: 0 0 5px 5px;
            background: $colorBrandDark;

            @include mq(lg) {
                align-self: flex-start;
                order: 2;
            }

            nav {
                @extend %flex;

                padding: $spacingXSm;

                a {
                    @include transition(color);

                    display: none;

                    padding: $spacingXSm $spacingBase;

                    text-decoration: none;
                    font-size: $fontSizeXSm;
                    font-weight: lighter;
                    line-height: $fontSizeSm;

                    color: $colorWhite;

                    @include mq(med) {
                        display: block;
                    }

                    &:first-child {
                        display: block;
                    }
                    &:last-child {
                        display: block;
                    }
                    &:not(:last-child) {
                        border-right: 1px solid $colorBrandMid;
                    }
                    &:hover {
                        color: $colorBrandBlue;
                    }
                    &:before {
                        font-size: $fontSizeSm;
                        line-height: $fontSizeSm;
                    }
                }
            }
        }
    }
}


.headerMain {
    .headerMain-mobileNav {
        @include transition(all .4s);

        position: fixed;
        z-index: 10000;
        top: 0;
        left: -250px;

        display: block;

        width: 250px;
        height: 100%;
        margin: 0;
        padding: 0;

        transform: translateZ(1px);

        background: rgba($colorBlack, .75);

        @include mq(lg) {
            display: none;
        }

        &.is-open {
            left: 0;

            &:after {
                width: 100vw;
            }
            .headerMain-mobileNav-toggle {
                transform: rotate(270deg);
            }
        }
        &:after {
            @include transition(all .4s);

            position: absolute;
            top: 0;
            left: 100%;

            width: 0;
            height: 100%;

            content: '';

            background: rgba($colorBrandLight, .65);
        }
        .headerMain-mobileNav-toggle {
            @include transition(all .4s);

            position: absolute;
            z-index: 10000;
            top: 15px;
            left: 265px;

            width: 35px;
            height: 35px;

            cursor: pointer;
            transform: rotate(0deg);

            color: $colorBrandBlue;
            border-radius: 50%;
            background: rgba($colorBlack,.65);
            box-shadow: 1px 1px 1px rgba(127, 127, 127, .3);

            &:before {
                @extend %iconStyle;

                width: 35px;
                height: 35px;
                margin-right: 0;
                margin-left: 0;

                content: '\e80e';
                text-align: center;
                font-size: 21px;
                line-height: 35px;
            }
        }
        .headerMain-mobileNav-drawer {
            position: relative;

            width: 100%;
            margin: 0;
            padding: 0;

            > nav {
                > ul {
                    @extend %unstyledList;

                    padding-top: $spacingXLg;

                    > li {
                        border-bottom: 1px solid $colorBrandLight;

                        &:first-child {
                            border-top: 1px solid $colorBrandLight;
                        }
                        > a {
                            @extend %transition;

                            display: block;

                            width: 100%;
                            height: 40px;
                            padding-left: 20px;

                            text-decoration: none;
                            letter-spacing: 1px;
                            text-transform: uppercase;
                            font-size: 16px;
                            line-height: 40px;

                            color: $colorBrandLight;
                            background: rgba(darken($colorBrandDark, 10%), .75);

                            &:hover {
                                background: rgba(darken($colorBrandDark, 30%), .75);
                            }
                            &:first-letter {
                                font-size: 20px;

                                color: $colorBrandYellow;
                            }
                        }
                        &.has-children {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}
