aside {
    @include fontSize(14px);

    position: relative;

    overflow: auto;

    margin: 10px;
    padding: 15px;

    letter-spacing: .2px;

    border-left: 4px solid rgba($colorBlack, .35);
    background: rgba($colorBlack, .05);

    &.quote {
        &:before {
            @extend %iconStyle;

            @include fontSize(24px);

            content: '\e813';
            transform: rotate(180deg) translate(2px, 2px) scale(.8);
        }
        span {
            position: relative;

            display: block;

            margin-top: 10px;
            margin-left: 20px;

            font-style: italic;

            &:before {
                position: absolute;
                top: -2px;
                right: calc(100% + 3px);
                bottom: 0;
                left: auto;

                margin: auto;

                content: '-';
            }
        }
    }
}
